import React, { useEffect } from 'react';
import { Button, Input } from 'reactstrap';

export const Rep = ({ item, onItemChange }) => {
  useEffect(() => {
    Object.keys(item).map(key => console.log(key + ' -> ' + item[key].value));
  }, []);
  if (!item.id) console.log("Data item must contain 'id' field");

  return (
    <tr>
      {Object.keys(item).map(key => {
        if (key === 'id') {
          return <th scope="row">{item[key].value}</th>;
        }
        return item[key].editable ? (
          <Input
            type={item[key].type}
            value={item[key].value}
            onChange={event =>
              onItemChange({ ...item, item: { value: event.target.value, type: item[key].type } })
            }
          />
        ) : (
          item.num.value
        );
      })}
      <td>
        {item.editable ? (
          <Input
            type={item.num.type}
            value={item.num.value}
            onChange={event =>
              onItemChange({ ...item, num: { value: event.target.value, type: item.num.type } })
            }
          />
        ) : (
          item.num.value
        )}
      </td>
      <td>
        {item.editable ? (
          <Input
            type={item.weigth.type}
            value={item.weigth.value}
            onChange={event =>
              onItemChange({
                ...item,
                weigth: { value: event.target.value, type: item.weigth.type },
              })
            }
          />
        ) : (
          item.weigth.value
        )}
      </td>
      <td>
        <Button color={item.editable ? 'primary' : 'danger'}>
          {item.editable ? 'edit' : 'done'}
        </Button>
      </td>
    </tr>
  );
};
